// 
const ProjectTypeEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' }
]
// 
const ProjectStatusEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' },
  { value: 3, label: '' }
]

// 
const StrategyTypeEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' }
]
export default {
  /********  ********/
  StrategyTypeEnum,
  // 
  strategyColumns: (that) => [
    {
      label: '',
      prop: 'StrategyName',
      minWidth: '40%'
    },
    {
      label: '',
      prop: 'EngineBrandName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'EngineModelName',
      minWidth: '20%'
      // formatter: (row, column, index) => {
      //   return row.Type ? StrategyTypeEnum.find((item) => item.value == row.Type).label : ''
      // }
    },
    {
      label: '',
      prop: 'Type',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.Type ? StrategyTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    }
  ],
  //  
  listOperates: (that) => [
    {
      label: '',
      method: (row) => {
        that.handleSelect(row)
      },
      setStyle: (row) => {
        if (that.selectItem && row.StrategyId == that.selectItem.StrategyId) return 'color: red;'
      },

      disabled: (row) => {
        return !row.IsUsing
      }
    }
  ],

  ProjectTypeEnum,
  ProjectStatusEnum,
  /********   ********/
  projectInfoColumns: (that) => [
    {
      label: '',
      prop: 'ProjectName',
      minWidth: '35%'
    },
    {
      label: '',
      prop: 'ProjectType',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.ProjectType
          ? ProjectTypeEnum.find((item) => item.value == row.ProjectType).label
          : ''
      }
    },
    {
      label: '',
      prop: 'ProjectNumber',
      minWidth: '35%'
    },
    {
      label: '',
      prop: 'TotalPower',
      minWidth: '20%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'Quantity',
      minWidth: '20%',
      unit: ''
    },
    {
      label: '',
      prop: 'ProjectArea',
      minWidth: '40%'
    },
    {
      label: '',
      prop: 'Status',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.Status ? ProjectStatusEnum.find((item) => item.value == row.Status).label : ''
      }
    },
    {
      label: '',
      prop: 'StartDate',
      minWidth: '30%',
      formatter: (row, column, index) => {
        return row.StartDate ? that.$moment(row.StartDate).format('YYYY.MM.DD') : ''
      }
    },
    {
      label: '',
      prop: 'EndDate',
      minWidth: '30%',
      formatter: (row, column, index) => {
        return row.EndDate ? that.$moment(row.EndDate).format('YYYY.MM.DD') : ''
      }
    },
    {
      label: '',
      prop: 'EndDateLast',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.EndDateLast || ''
      }
    }
  ],

  /********   ********/
  gensetInfoColumns: (that) => [
    {
      label: '',
      prop: 'DisplayName',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'CollectorToken',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'EngineModel',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'RatedPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '',
      prop: 'RunHourTotal',
      minWidth: '15%',
      unit: 'h'
    },
    {
      label: '',
      prop: 'InstallationSite',
      minWidth: '25%',
      render: (h, parmas) => {
        return h(
          'span',
          {
            class: 'text-overflow-ellipsis MulLineHiding', //
            attrs: {
              title: parmas.row.InstallationSite || ''
            }
          },
          parmas.row.InstallationSite
        )
      }
    },
    {
      label: '',
      prop: 'IsParallel',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return row.IsParallel ? '' : ''
      }
    },
    {
      label: '',
      prop: 'IsFenceOpen',
      minWidth: '18%',
      formatter: (row, column, index) => {
        return row.IsFenceOpen ? '' : ''
      }
    },
    {
      label: '',
      prop: 'IsStrategy',
      minWidth: '18%',
      formatter: (row, column, index) => {
        return row.IsStrategy ? '' : ''
      }
    }
  ]
}



