// 
const CostTypeEnum = [
  { value: 1, label: '' },
  { value: 2, label: '' }
]
// 
const CostItemsEnum = []
export default {
  /********  ********/
  CostTypeEnum,

  costManageColumns: (that) => [
    {
      label: '',
      prop: 'Type',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.Type ? CostTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    },
    {
      label: '',
      prop: 'SortName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'Money',
      minWidth: '20%',
      unit: ''
    },
    {
      label: '',
      prop: 'CostDetail',
      minWidth: '35%'
    },

    {
      label: '',
      prop: 'FileUrl',
      minWidth: '18%',
      formatter: (row, column, index) => {
        // #3BB19C
        return row.FileUrl ? '<span style="color:#409EFF;cursor: pointer"></span>' : ''
      },
      click: (row, index) => {
        console.log(row, index, that)
        that.handleClickShowImg(row, index)
      }
    },
    {
      label: '',
      prop: 'CreateUserName',
      minWidth: '20%'
    },
    {
      label: '',
      prop: 'CreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    }
  ],
  // 
  costAggregationColumns: (that) => [
    {
      label: '',
      prop: 'Type',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.Type ? CostTypeEnum.find((item) => item.value == row.Type).label : ''
      }
    },
    {
      label: '',
      prop: 'SortName',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'AllMoney',
      minWidth: '25%',
      unit: '',
      formatter: (row, column, index) => {
        return row.AllMoney || 0
      }
    },
    {
      label: '',
      prop: 'AllCount',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.AllCount || 0
      }
    }
  ],

  // 
  CompletionReportColumns: (that) => [
    {
      label: '',
      prop: 'ProgressName',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'CollectorToken',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount == 0 ? '' : ''
      }
    },
    {
      label: '',
      prop: 'LastCreateUserName',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.LastCreateUserName || '-'
      }
    },
    {
      label: '',
      prop: 'LastCreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount > 0
          ? row.LastCreateTime
            ? that.$moment(row.LastCreateTime).format('yyyy-MM-DD HH:mm:ss')
            : ''
          : '-'
      }
    }
  ],

  // 
  CustomerSatisfactionColumns: (that) => [
    {
      label: '',
      prop: 'ProgressName',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'CollectorToken',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount == 0 ? '' : ''
      }
    },
    {
      label: '',
      prop: 'LastCreateUserName',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.LastCreateUserName || '-'
      }
    },
    {
      label: '',
      prop: 'LastCreateTime',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount > 0
          ? row.LastCreateTime
            ? that.$moment(row.LastCreateTime).format('yyyy-MM-DD HH:mm:ss')
            : ''
          : '-'
      }
    }
  ],

  // 
  OperationReportColumns: (that) => [
    {
      label: '',
      prop: 'ProgressName',
      minWidth: '25%'
    },
    {
      label: '',
      prop: 'CollectorToken',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.RecordCount == 0 ? '' : ''
      }
    },
    {
      label: '',
      prop: 'LastCreateUserName',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.LastCreateUserName || '-'
      }
    },
    {
      label: '',
      prop: 'LastCreateTime',
      minWidth: '50%',
      formatter: (row, column, index) => {
        return row.RecordCount > 0
          ? row.LastCreateTime
            ? that.$moment(row.LastCreateTime).format('yyyy-MM-DD HH:mm:ss')
            : ''
          : '-'
      }
    }
  ]
}



