<template>
  <div>
    <el-dialog
      title=""
      class="show-file-dialog"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="close()"
    >
      <div class="dialog-content">
        <el-row>
          <el-col>
            <span></span
            ><span>{{
              ProjectFileTypeEnum.find((item) => item.value == fileRecordInfo.FileType).label
            }}</span>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <span></span><span>{{ fileRecordInfo.FileName }}</span>
          </el-col>
          <el-col :span="12">
            <span></span><span>{{ fileRecordInfo.FileDetail }}</span>
          </el-col>
        </el-row>
        <p><span></span></p>
        <TableList
          class="file-list-table"
          :options="options"
          :columns="fileListColumns"
          :dataSource="dataSource"
          border
          v-loading="loading"
        >
          <el-table-column
            slot="operation"
            label=""
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group">
                <el-button
                  v-if="!IsImgType(scope.row.FileType)"
                  type="text"
                  @click="handleDownloadClick(scope.row)"
                >
                  
                </el-button>
                <el-button
                  v-if="IsImgType(scope.row.FileType)"
                  type="text"
                  @click="handlePreviewClick(scope.row)"
                >
                  
                </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="close()"> </el-button>
        <el-button type="primary" @click="close()"> </el-button>
      </div>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="previewDialogVisible"
      append-to-body
      class="previewDialog"
    >
      <img width="100%" :src="dialogImageUrl" alt="" />
    </el-dialog>
  </div>
</template>

<script>
import _data from './data.js'
import { DownLoadFileByFileReader, DownLoadFileByCreateObjectURL } from '@/utils/util'
const { ProjectFileTypeEnum } = _data

export default {
  name: 'FileListDialog',
  props: {
    fileRecordInfo: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,
      ProjectFileTypeEnum, // 
      fileListColumns: _data.fileListColumns(this), //  
      // 
      options: {
        index: true, //
        labelIndex: '',
        props: {
          width: 120 // 
        }
      },
      dataSource: [],

      previewDialogVisible: false,
      dialogImageUrl: ''
    }
  },
  mounted() {
    console.log(this.fileRecordInfo)
    this.getFileList()
  },
  methods: {
    // 
    getFileList() {
      this.loading = true
      const query = { id: this.fileRecordInfo.ProjectFileId }
      this.$api.project.GetGensetProjectFile(query).then((res) => {
        if (res.Code === 1) {
          console.log(res)
          this.fileDetails = res.Data
          this.$set(this, 'dataSource', res.Data.FileUrlList)
        }
        this.loading = false
      })
    },
    IsImgType(type) {
      return ['png', 'jpg', 'jpge', 'gif'].includes(type)
    },
    close() {
      this.$emit('closeDialog')
    },
    // 
    handleDownloadClick(row) {
      const query = { id: row.ProjectFileUrlId }
      this.$api.project.DoDownloadGensetProjectFile(query).then((res) => {
        console.log(res)
        // DownLoadFileByFileReader(row.FileName, res.data)
        DownLoadFileByCreateObjectURL(res, row.FileName)
      })
    },
    // 
    handlePreviewClick(row) {
      this.previewDialogVisible = true
      this.dialogImageUrl = `${process.env.BASE_API}/${row.FileUrl}`
    }
  }
}
</script>

<style lang="less" scoped>
.dialog-content {
  .el-row {
    line-height: 24px;
    margin-bottom: 16px;
  }
  p {
    margin-bottom: 8px;
  }
  .file-list-table {
    margin-bottom: 16px;
  }
}
</style>



