 * @LastEditors: linxu
 * @LastEditTime: 2022-08-04 14:53:36
 * @Description: /  
-->
<template>
  <el-dialog
    :title="parallelCerwInfo ? '' : ''"
    class="add-crew-dialog"
    width="90%"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="close()"
  >
    <div class="dialog-content">
      <div class="left-box">
        <p class="title"></p>
        <div class="search-box">
          <div class="search-item">
            <span>:</span>
            <!--  -->
            <position-cascader
              placeholder=""
              :defaultPosition="searchForm.InstallationSite"
              @positionChange="positionChange"
            />
          </div>
          <div class="search-item">
            <span>:</span>
            <el-input
              placeholder=""
              maxlength="20"
              v-model="searchForm.EngineModel"
            >
            </el-input>
          </div>
          <div class="search-item power-item">
            <span>:</span>
            <el-input type="number" placeholder="" v-model="searchForm.RatedPowerMin">
            </el-input>
            <span>-</span>
            <el-input type="number" placeholder="" v-model="searchForm.RatedPowerMax">
            </el-input>
          </div>
          <div class="search-btn-box">
            <el-button type="text" class="reset-btn" @click="reset()"></el-button>
            <el-button type="primary" class="query-btn" @click="queryTableData()"> </el-button>
          </div>
        </div>
        <!--  -->
        <TableList
          :options="options"
          :columns="columns"
          :dataSource="dataSource"
          border
          v-loading="loading"
        >
          <!-- :pagination="paginationCfg"
          @handleChangePage="handleChangePage"
          @handleChangeSize="handleChangeSize" -->
          <el-table-column slot="operation" label="" align="center" width="100">
            <template slot-scope="scope">
              <div class="operate-group" style="text-align: center">
                <el-button
                  :disabled="scope.row.isSelect"
                  type="text"
                  @click="handleSelect(scope.row, 1)"
                >
                  
                </el-button>

                <el-button
                  :disabled="scope.row.isSelect"
                  type="text"
                  @click="handleSelect(scope.row, 2)"
                >
                  
                </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>
      </div>
      <div class="right-box">
        <p class="title"></p>
        <!--  -->
        <p class="select-type"><span></span></p>
        <TableList :options="options" :columns="columns" :dataSource="selectHostData" border>
          <el-table-column
            slot="operation"
            label=""
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group" style="text-align: center">
                <el-button type="text" @click="handleRemove(scope.row, 1)">  </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>

        <p class="select-type"><span></span> </p>
        <TableList :options="options" :columns="columns" :dataSource="selectExtensionData" border>
          <el-table-column
            slot="operation"
            label=""
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group" style="text-align: center">
                <el-button type="text" @click="handleRemove(scope.row, 2)">  </el-button>
              </div>
            </template>
          </el-table-column>
        </TableList>

        <p class="all-power">
          <span>{{ TotalPower }}</span
          >Kw
        </p>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="close()"> </el-button>
      <el-button type="primary" @click="submitForm()"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import PositionCascader from '@/components/PositionCascader'
import _data from './data.js'

export default {
  name: 'AddOrEditParallelCrewDialog',

  components: {
    PositionCascader
  },
  props: {
    projectId: {
      type: Number,
      default: null
    },
    parallelCerwInfo: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,
      // 
      searchForm: {
        InstallationSite: '',
        EngineModel: '',
        RatedPowerMin: '',
        RatedPowerMax: ''
      },

      // 
      columns: _data.setParallelCrewColumns(this),
      dataSource: [], //  
      selectHostData: [], // 
      selectExtensionData: [], // 
      options: {
        index: true, //
        labelIndex: '',
        props: {
          width: 60 // 
        }
      },
      ParallelName: '',
      TotalPower: 0
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    queryTableData() {
      this.loading = true
      // console.log('searchForm------', this.searchForm, this.parallelCerwInfo)
      const params = {
        ...this.searchForm,
        ProjectId: this.projectId,
        ParallelType: 0 // 0:;1:,2:
      }
      this.$api.project
        .GetGensetProjectJoinGensetByAll(params)
        .then((res) => {
          if (res.Code === 1) {
            // 
            const data = res.Data.map((item) => {
              return {
                ...item.GeneratorSet,
                IsParallel: item.IsParallel,
                isSelect: false
              }
            })
            //-- --
            const newData = [],
              hostData = [],
              extensionData = []
            if (!!this.parallelCerwInfo) {
              console.log('edit')
              data.forEach((item) => {
                if (!item.IsParallel) {
                  // 
                  newData.push(item)
                } else if (this.parallelCerwInfo.selectHostIds.includes(item.GeneratorSetId)) {
                  // 
                  newData.push({ ...item, isSelect: true })
                  hostData.push({ ...item, isSelect: true })
                } else if (this.parallelCerwInfo.selectExtensionIds.includes(item.GeneratorSetId)) {
                  // 
                  newData.push({ ...item, isSelect: true })
                  extensionData.push({ ...item, isSelect: true })
                }
              })
              console.log(this.parallelCerwInfo, hostData, extensionData)
              this.$set(this, 'selectHostData', hostData)
              this.$set(this, 'selectExtensionData', extensionData)

              this.countTotalPower()
            } else {
              data.forEach((item) => {
                if (!item.IsParallel) {
                  newData.push(item)
                }
              })
            }
            this.$set(this, 'dataSource', newData)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    reset() {
      this.$set(this, 'searchForm', {
        InstallationSite: '',
        EngineModel: '',
        RatedPowerMin: '',
        RatedPowerMax: ''
      })
    },
    // 
    /**
     * @type 1: 2.
     */
    handleSelect(row, type) {
      if (type === 1) {
        if (this.selectHostData.length > 0) {
          this.$message.warning('')
          return
        }
        this.selectHostData.push(row)
      } else {
        this.selectExtensionData.push(row)
      }
      this.dataSource.splice(this.dataSource.indexOf(row), 1, { ...row, isSelect: true })
      this.$set(this, 'dataSource', this.dataSource)
      this.countTotalPower()
    },
    // 
    /**
     * @type 1: 2.
     */
    handleRemove(row, type) {
      if (type === 1) {
        this.selectHostData.splice(this.selectHostData.indexOf(row), 1)
      } else {
        this.selectExtensionData.splice(this.selectExtensionData.indexOf(row), 1)
      }
      this.dataSource.splice(
        this.dataSource.findIndex((item) => item.GeneratorSetId == row.GeneratorSetId),
        1,
        { ...row, isSelect: false }
      )
      this.countTotalPower()
    },

    // 
    countTotalPower() {
      console.log(this.selectHostData, this.selectExtensionData)
      const allSelect = this.selectHostData.concat(this.selectExtensionData)
      let sum = 0
      allSelect.forEach((item) => {
        sum += item.RatedPower
      })
      this.TotalPower = sum
    },

    // 
    submitForm() {
      if (this.selectHostData.length == 0) {
        this.$message.warning('')
        return
      }
      if (this.selectExtensionData.length == 0) {
        this.$message.warning('')
        return
      }
      if (!!this.parallelCerwInfo) {
        console.log('save----')
        this.editProjectParallel()
        return
      }
      console.log('save----')
      this.addProjectParallel()
    },
    // 
    addProjectParallel() {
      const params = {
        ProjectId: this.projectId,
        JoinGensetIsMainList: this.selectHostData.map((item) => {
          return { GeneratorSetId: item.GeneratorSetId }
        }),
        JoinGensetIsNotMainList: this.selectExtensionData.map((item) => {
          return { GeneratorSetId: item.GeneratorSetId }
        }),
        ParallelName: this.ParallelName,
        TotalPower: this.TotalPower
      }
      this.$api.project.AddGensetProjectParallel(params).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },
    // 
    editProjectParallel() {
      const query = {
        id: this.parallelCerwInfo.ID
      }
      const params = {
        // ProjectId: this.projectId,
        JoinGensetIsMainList: this.selectHostData.map((item) => {
          return { GeneratorSetId: item.GeneratorSetId }
        }),
        JoinGensetIsNotMainList: this.selectExtensionData.map((item) => {
          return { GeneratorSetId: item.GeneratorSetId }
        }),
        ParallelName: this.ParallelName,
        TotalPower: this.TotalPower
      }
      this.$api.project.EditGensetProjectParallel(params, query).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: '!'
          })
        } else {
          this.$message({
            type: 'error',
            message: '!'
          })
        }
        this.$emit('closeDialog', { refresh: true })
      })
    },
    close() {
      this.$emit('closeDialog')
    },

    // --change
    positionChange(data) {
      // console.log(data)
      this.searchForm.InstallationSite = data.crewPosition
    }
  }
}
</script>

<style lang="less" scoped>
.dialog-content {
  display: flex;
  justify-content: space-between;
  .left-box,
  .right-box {
    width: calc(50% - 8px);
    border: 1px solid #e4e4e4;
    > div {
      margin: 8px 16px 8px;
    }
    .title {
      line-height: 40px;
      font-weight: 700;
      border-bottom: 1px solid #e4e4e4;
      text-indent: 8px;
    }
  }

  .search-box .search-item {
    width: 50%;
    &.power-item {
      width: 60%;
    }
  }
  .right-box {
    .select-type {
      padding: 8px 16px 0;
      line-height: 20px;
      span {
        display: inline-block;
        width: 4px;
        height: 16px;
        background-color: #1abc9c;
        vertical-align: middle;
        margin-right: 8px;
      }
    }
    .all-power {
      margin: 16px 16px 8px;
      text-align: right;
      span {
        color: #1abc9c;
        font-weight: 700;
        margin-right: 4px;
      }
    }
  }
}
</style>



